import React, { Component } from "react";
import "../css/header.css";
import resume from "../documents/ResumePublic.pdf";
import { faArrowCircleDown } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
const headerImg = require.context("../../public/images", true);
const lightbulbs = headerImg("./lightsSM.jpg");

class Header extends Component {
  render() {
    return (
      <div className="headerContainer">
        <div className="headerSubContainer">
          <div className="headerTitle">
            <h1>Tony Lecca</h1>
            <h3>Web Development</h3>
            <div className="headerLinks">
              <a
                href="https://github.com/TonyRamirezLecca"
                target="_blank"
                rel="noopener noreferrer"
              >
                Github
              </a>
              <a
                href="https://www.linkedin.com/in/tonyramirezlecca/"
                target="_blank"
                rel="noopener noreferrer"
              >
                LinkedIn
              </a>
              <a href={resume} target="_blank" rel="noopener noreferrer">
                Resume
              </a>
            </div>
            <div className="arrowDown">
              <div className="arrowDownContainer">
                <a href="#portfolio">
                  <FontAwesomeIcon icon={faArrowCircleDown} />
                </a>
              </div>
            </div>
          </div>
          <div className="headerAbout">
            <img src={lightbulbs} className="lightbulbImg" alt="lightbulbs" />
            <p>A Developer from Provo, UT.</p>
          </div>
        </div>
      </div>
    );
  }
}

export default Header;
