import React, { Component } from "react";
import "../css/about.css";
const images = require.context("../../public/images", true);
const picOfMe = images("./me.jpg");

class About extends Component {
  render() {
    return (
      <div id="aboutContainer">
        <div id="aboutSubContainer">
          <div id="aboutText">
            <h3 id="aboutTitleInner">About Me</h3>
            <p>
              I'm Tony Ramirez Lecca. Born in Peru. Raised in Utah. <br />
              <br />
              I've always loved music, art, and design, but somehow I ended up
              studying Computer Science at Brigham Young University. I don't
              think that was an accident. When I discovered web development, I
              realized that programming and an eye for design <em>could</em> go
              together. <br />
              <br />
              After discovering my niche, I spent the next year focusing my
              studies on front-end web development. There's still a lot I don't
              know, but there's not a lot I can't learn. I love challenging
              myself and exploring the world of programming.
            </p>
          </div>
          <h3 id="aboutTitleOuter">About Me</h3>
        </div>
      </div>
    );
  }
}

export default About;
