import React, { Component } from "react";
import "../css/portfolioSection.css";

class PortfolioSection extends Component {
  state = {};

  showLink = () => {
    return this.props.title !== this.props.buttonClicked
      ? "portfolioImgLinks"
      : "portfolioImgLinks portfolioLinksVisible";
  };

  loadImage = () => {
    this.forceUpdate();
  };

  render() {
    return (
      <div
        className="portfolioSectionContainer"
        onClick={() => this.props.showLinks(this.props.title)}
      >
        <h1 className="portfolioHeader">{this.props.title}</h1>

        <div className="portfolioImgContainer">
          <img
            src={this.props.img}
            className="portfolioImg"
            onLoad={this.loadImage}
            alt="Cannot Display"
          />
          <div className={this.showLink()}>
            <a
              href={this.props.github}
              target="_blank"
              rel="noopener noreferrer"
            >
              Github
            </a>
            <a href={this.props.url} target="_blank" rel="noopener noreferrer">
              Site
            </a>
          </div>
        </div>
      </div>
    );
  }
}

export default PortfolioSection;
