import React, { Component } from "react";
import PortfolioSection from "./PortfolioSection";
import PortfolioButton from "./PortfolioButton";
import "../css/portfolio.css";
const images = require.context("../../public/images", true);
const portfolioImg = images("./portfolio.jpg");
const weatherImg = images("./weather.jpg");
const comicImg = images("./xkcdComics.jpg");
const googleMapsImg = images("./mapAPI.jpg");
const americanTaxiImg = images("./americanTaxi.jpg");

class Portfolio extends Component {
  constructor() {
    super();
    this.state = {
      portfolioSections: [
        {
          id: 1,
          title: "Portfolio",
          img: portfolioImg,
          github: "https://github.com/TonyRamirezLecca/portfolioWebsite",
          url: "http://www.tonylecca.com/",
          hasHTML5: true,
          hasCSS3: true,
          hasVue: false,
          hasReact: true,
          hasJavascript: true,
          hasNodeJS: false,
          hasPHP: false,
          hasRESTAPI: false,
          hasMySQL: false
        },
        {
          id: 3,
          title: "Weather API",
          img: weatherImg,
          github: "https://github.com/TonyRamirezLecca/weatherForecastAPI",
          url: "http://weather.tonylecca.com/",
          hasHTML5: true,
          hasCSS3: true,
          hasVue: false,
          hasReact: false,
          hasJavascript: true,
          hasNodeJS: false,
          hasPHP: false,
          hasRESTAPI: true,
          hasMySQL: false
        },
        {
          id: 5,
          title: "XKCD Comics",
          img: comicImg,
          github: "https://github.com/TonyRamirezLecca/xkcdComicGenerator",
          url: "http://comics.tonylecca.com/",
          hasHTML5: true,
          hasCSS3: true,
          hasVue: true,
          hasReact: false,
          hasJavascript: true,
          hasNodeJS: false,
          hasPHP: false,
          hasRESTAPI: true,
          hasMySQL: false
        },
        {
          id: 6,
          title: "Google Maps API",
          img: googleMapsImg,
          github: "https://github.com/TonyRamirezLecca/MapAPI",
          url: "http://valley.tonylecca.com/",
          hasHTML5: true,
          hasCSS3: true,
          hasVue: false,
          hasReact: false,
          hasJavascript: true,
          hasNodeJS: false,
          hasPHP: false,
          hasRESTAPI: true,
          hasMySQL: false
        },
        {
          id: 7,
          title: "American Taxi",
          img: americanTaxiImg,
          github: "https://github.com/TonyRamirezLecca/AmericanTaxi",
          url: "http://americantaxiutah.com/",
          hasHTML5: true,
          hasCSS3: true,
          hasVue: false,
          hasReact: false,
          hasJavascript: true,
          hasNodeJS: false,
          hasPHP: false,
          hasRESTAPI: true,
          hasMySQL: false
        }
      ],
      filteredPortfolio: [
        {
          id: 1,
          title: "Portfolio",
          img: portfolioImg,
          github: "https://github.com/TonyRamirezLecca/portfolioWebsite",
          url: "http://www.tonylecca.com/",
          hasHTML5: true,
          hasCSS3: true,
          hasVue: false,
          hasReact: true,
          hasJavascript: true,
          hasNodeJS: false,
          hasPHP: false,
          hasRESTAPI: false,
          hasMySQL: false
        },
        {
          id: 3,
          title: "Weather API",
          img: weatherImg,
          github: "https://github.com/TonyRamirezLecca/weatherForecastAPI",
          url: "http://weather.tonylecca.com/",
          hasHTML5: true,
          hasCSS3: true,
          hasVue: false,
          hasReact: false,
          hasJavascript: true,
          hasNodeJS: false,
          hasPHP: false,
          hasRESTAPI: true,
          hasMySQL: false
        },
        {
          id: 5,
          title: "XKCD Comics",
          img: comicImg,
          github: "https://github.com/TonyRamirezLecca/xkcdComicGenerator",
          url: "http://comics.tonylecca.com/",
          hasHTML5: true,
          hasCSS3: true,
          hasVue: true,
          hasReact: false,
          hasJavascript: true,
          hasNodeJS: false,
          hasPHP: false,
          hasRESTAPI: true,
          hasMySQL: false
        },
        {
          id: 6,
          title: "Google Maps API",
          img: googleMapsImg,
          github: "https://github.com/TonyRamirezLecca/MapAPI",
          url: "http://valley.tonylecca.com/",
          hasHTML5: true,
          hasCSS3: true,
          hasVue: false,
          hasReact: false,
          hasJavascript: true,
          hasNodeJS: false,
          hasPHP: false,
          hasRESTAPI: true,
          hasMySQL: false
        },
        {
          id: 7,
          title: "American Taxi",
          img: americanTaxiImg,
          github: "https://github.com/TonyRamirezLecca/AmericanTaxi",
          url: "http://americantaxiutah.com/",
          hasHTML5: true,
          hasCSS3: true,
          hasVue: false,
          hasReact: false,
          hasJavascript: true,
          hasNodeJS: false,
          hasPHP: false,
          hasRESTAPI: true,
          hasMySQL: false
        }
      ],
      filterButtons: [
        {
          name: "All"
        },
        {
          name: "HTML5"
        },
        {
          name: "CSS3"
        },
        {
          name: "Vue"
        },
        {
          name: "React"
        },
        {
          name: "Javascript"
        },
        {
          name: "NodeJS"
        },
        {
          name: "PHP"
        },
        {
          name: "MySQL"
        },
        {
          name: "API"
        }
      ],
      isLoading: false,
      buttonClicked: "",
      projectsBeingFiltered: ""
    };
  }

  filterPortfolio = buttonName => {
    this.setState({ projectsBeingFiltered: buttonName });
    this.loadingScreen();
    setTimeout(() => {
      let filteredPortfolio = [];
      switch (buttonName) {
        case "All":
          filteredPortfolio = this.state.portfolioSections;
          break;
        case "HTML5":
          filteredPortfolio = this.state.portfolioSections.filter(
            e => e.hasHTML5
          );
          break;
        case "HTML5":
          filteredPortfolio = this.state.portfolioSections.filter(
            e => e.hasCSS3
          );
          break;
        case "Vue":
          filteredPortfolio = this.state.portfolioSections.filter(
            e => e.hasVue
          );
          break;
        case "React":
          filteredPortfolio = this.state.portfolioSections.filter(
            e => e.hasReact
          );
          break;
        case "Javascript":
          filteredPortfolio = this.state.portfolioSections.filter(
            e => e.hasJavascript
          );
          break;
        case "NodeJS":
          filteredPortfolio = this.state.portfolioSections.filter(
            e => e.hasNodeJS
          );
          break;
        case "PHP":
          filteredPortfolio = this.state.portfolioSections.filter(
            e => e.hasPHP
          );
          break;
        case "API":
          filteredPortfolio = this.state.portfolioSections.filter(
            e => e.hasRESTAPI
          );
          break;
        case "MySQL":
          filteredPortfolio = this.state.portfolioSections.filter(
            e => e.hasMySQL
          );
          break;
        default:
          filteredPortfolio = this.state.portfolioSections;
          break;
      }
      this.setState({ filteredPortfolio });
    }, 1000);
  };

  loadingScreen = () => {
    this.setState({ isLoading: true });
    setTimeout(() => {
      this.setState({ isLoading: false });
    }, 2000);
  };

  showLinks = title => {
    this.setState({ buttonClicked: title });
  };

  loadingScreenStyle() {
    return !this.state.isLoading
      ? "loadingScreen"
      : "loadingScreen loadingScreenVisible";
  }
  loadingScreenTextStyle() {
    return !this.state.isLoading
      ? "loadingScreenText"
      : "loadingScreenText loadingTextFadeIn";
  }
  loadingScreenSliderTop() {
    return !this.state.isLoading
      ? "loadingTopSlider"
      : "loadingTopSlider loadingSlidersSlideInLeft";
  }
  loadingScreenSliderBottom() {
    return !this.state.isLoading
      ? "loadingBottomSlider"
      : "loadingBottomSlider loadingSlidersSlideInRight";
  }

  render() {
    return (
      <div className="portfolioContainer">
        <div className="portfolioTitle">
          <h1>My Projects</h1>
        </div>
        <div className={this.loadingScreenStyle()}>
          <div className={this.loadingScreenTextStyle()}>{`Filtering ${
            this.state.projectsBeingFiltered
          } Projects...`}</div>
          <div className={this.loadingScreenSliderTop()} />
          <div className={this.loadingScreenSliderBottom()} />
        </div>
        <div className="buttonsContainer">
          {this.state.filterButtons.map(button => {
            return (
              <PortfolioButton
                key={button.name}
                name={button.name}
                filterPortfolio={this.filterPortfolio}
              />
            );
          })}
        </div>
        <div className="portfolioGrid">
          {this.state.filteredPortfolio.map(section => {
            return (
              <PortfolioSection
                key={section.id}
                title={section.title}
                showLinks={this.showLinks}
                buttonClicked={this.state.buttonClicked}
                img={section.img}
                github={section.github}
                url={section.url}
              />
            );
          })}
        </div>
      </div>
    );
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({ filteredPortfolio: [] });
      setTimeout(() => {
        this.setState({ filteredPortfolio: this.state.portfolioSections });
      }, 100);
    }, 500);
  }
}

export default Portfolio;
